<template>
  <div class="m-order-table">
    <div class="search" style="margin: 15px 0;width: 400px;display:inline-block;vertical-align:middle;">
      <el-input placeholder="" v-model="searchKeyword" class="input-with-select">
        <el-select v-model="searchSelect" slot="prepend" placeholder="">
          <el-option label="" value="group_leader"></el-option>
          <el-option label="" value="user_name"></el-option>
          <el-option label="" value="customer_name"></el-option>
          <el-option label="" value="contract_number"></el-option>
          <el-option label="" value="car_license"></el-option>
        </el-select>
        <el-button @click="search" slot="append" icon="el-icon-search"></el-button>
      </el-input>
    </div>
    <div style="display: inline-block;">
      <el-select v-if="noAction" style="width: 150px;" v-model="filter.bill_status" clearable placeholder="" @change="handleFilterStatusChange">
        <el-option
          v-for="item in statusDict"
          :key="item.value"
          :label="item.name"
          :value="item.value">
        </el-option>
      </el-select>
      <el-select style="width: 150px;" v-model="filter.company_id" clearable placeholder="" @change="handleFilterCompanyChange">
        <el-option
          v-for="item in companyDict"
          :key="item.company_id"
          :label="item.company_name"
          :value="item.company_id.toString()">
        </el-option>
      </el-select>
      <el-date-picker
        @change="handleFilterDateRangeChange"
        v-model="filter.dateRange"
        value-format="yyyy-MM-dd"
        type="daterange"
        range-separator=""
        start-placeholder=""
        end-placeholder="">
      </el-date-picker>
    </div>
    <el-table
      v-loading="loading"
      :data="rows_inShow"
      stripe
      style="width: 100%">
      <el-table-column
        prop="bill_id"
        label="">
      </el-table-column>
      <el-table-column
        label=""
        :width="100">
        <template slot-scope="scope">{{ scope.row.customer_name }}</template>
      </el-table-column>
      <el-table-column
        v-for="col in cols"
        :key="col.prop"
        :prop="col.prop"
        :label="col.label"
        :width="col.width">
      </el-table-column>
      <el-table-column
        v-if="!noAction"
        fixed="right"
        label=""
        :width="stage === '3'?260:160">
        <template slot-scope="scope">
          <div>
            <el-button @click="detail(scope.row)" type="text" size="small"></el-button>
            <el-button v-if="stage !== '3'" @click="audit(scope.row)" type="text" size="small"></el-button>
            <el-button v-if="stage === '3'" @click="auditP(scope.row)" type="text" size="small">()</el-button>
            <el-button v-if="stage === '3'" @click="auditC(scope.row)" type="text" size="small">()</el-button>
            <el-button @click="reject(scope.row)" type="text" size="small"></el-button>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        v-else
        fixed="right"
        label=""
        width="120">
        <template slot-scope="scope">
          <div>
            <el-button @click="detail(scope.row)" type="text" size="small"></el-button>
            <el-button @click="deleteOrder(scope.row)" type="text" size="small"></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="margin: 20px 0;text-align:center;"
      background
      :page-size="page_size"
      :total="total"
      layout="prev, pager, next"
      :current-page="page"
      @current-change="handlePageChange">
    </el-pagination>
  </div>
</template>
<script>
import { rest } from '@/common/request';
import { percentDisplay, currencyDisplay } from '@/common/utils';

export default {
  props:['noAction','stage'],
  data(){
    return{
      cols:[
        {
          prop: 'company',
          label: '',
          width: 150
        },
        {
          prop: 'car_lincese',
          label: '',
          width: 100
        },
        {
          prop: 'product',
          label: '',
          width: 120
        },
        {
          prop: 'load_type',
          label: ''
        },
        {
          prop: 'month_rate',
          label: ''
        },
        {
          prop: 'bill_status',
          label: '',
          width: 120
        },
        {
          prop: 'loan_amount',
          label: '',
          width: 120
        },
        {
          prop: 'contract_amount',
          label: '',
          width: 120
        },
        {
          prop: 'loan_period',
          label: ''
        },
        {
          prop: 'loan_start',
          label: '',
          width: 100
        },
        {
          prop: 'loan_end',
          label: '',
          width: 100
        },
        {
          prop: 'creadit',
          label: '',
          width: 100
        },
        {
          prop: 'group',
          label: '',
          width: 100
        },
        {
          prop: 'username',
          label: ' ',
          width: 100
        },
        {
          prop: 'group_leader',
          label: ' ',
          width: 100
        }
      ],
      rows: [],
      loading: true,
      page: 1,
      page_size: 20,
      total: 0,
      searchSelect: null,
      searchKeyword: null,
      filter:{
        bill_status: '',
        company_id: '',
        dateRange: null
      },
      companyDict: [],
      statusDict:[
        {name: '', value: '-1'},
        {name: '', value: '0'},
        {name: '', value: '1'},
        {name: '', value: '2'},
        {name: '', value: '3'},
        {name: '', value: '4'},
        {name: '', value: '5'}
      ]
    }
  },
  computed:{
    'rows_inShow'(){
      let statusDict = {
        '-1':'',
        '0':'',
        '1':'',
        '2':'',
        '3':'',
        '4':'',
        '5':''
      }
      let loadProductDict = {
        '1': '',
        '2': ''
      }
      let loadTypeDict = {
        '0': '-',
        '1': '',
        '2': ''
      }
      let rows_inShow = this.rows.map(item => {
        item.bill_status = statusDict[item.bill_status.toString()];
        item.loan_period = item.loan_period + '';
        item.loan_amount = currencyDisplay(item.loan_amount);
        item.contract_amount = currencyDisplay(item.contract_amount);
        item.month_rate = percentDisplay(item.month_rate);
        item.creadit?item.creadit = '':item.creadit = '';
        item.product = loadProductDict[item.product];
        item.load_type = loadTypeDict[item.load_type];
        return item
      })
      return rows_inShow;
    }
  },
  methods:{
    fetchCompanyDict(){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/dict',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'GET'
      }).then(res => {
        this.companyDict = res.data.company;
      })
    },
    fetchList(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/bill',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.rows = res.data.data;
        this.total = res.data.total;
        this.loading = false;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    reloadList(){
      let data = {
        company_id: this.filter.company_id,
        bill_status: this.stage,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      if(this.searchSelect){data[this.searchSelect] = this.searchKeyword;}
      if(this.page === 1){
        this.fetchList(data);
      }else{
        this.page = 1;
      }
    },
    deleteItem(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/bill/del',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    deleteOrder(row){
      let data = {
        company_id: this.filter.company_id,
        bill_status: this.stage,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteItem({bill_id: row.bill_id}).then(res => {
          this.fetchList(data);
        });
      }).catch(() => {});
    },
    detail(row){
      this.$emit('detail', row);
    },
    audit(row){
      this.$emit('audit', row);
    },
    auditP(row){
      this.$emit('auditPerson', row);
    },
    auditC(row){
      this.$emit('auditCompany', row);
    },
    reject(row){
      this.$emit('reject', row);
    },
    handlePageChange(val){
      this.page = val;
      let data = {
        company_id: this.filter.company_id,
        order: "asc",
        page: val,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      if(this.searchSelect){data[this.searchSelect] = this.searchKeyword;}
      this.fetchList(data);
    },
    search(){
      let data = {
        company_id: this.filter.company_id,
        bill_status: this.stage,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      if(this.searchSelect){data[this.searchSelect] = this.searchKeyword;}
      if(this.page === 1){
        this.fetchList(data);
      }else{
        this.page = 1;
      }
    },
    handleFilterStatusChange(val){
      let data = {
        company_id: this.filter.company_id,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?val:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      if(this.page === 1){
        this.fetchList(data);
      }else{
        this.page = 1;
      }
    },
    handleFilterCompanyChange(val){
      let data = {
        company_id: val,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      if(this.page === 1){
        this.fetchList(data);
      }else{
        this.page = 1;
      }
    },
    handleFilterDateRangeChange(val){
      let data = {
        company_id: this.filter.company_id,
        bill_status: this.stage,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: val?val[0]:'',
        loan_end: val?val[1]:''
      }
      if(this.page === 1){
        this.fetchList(data);
      }else{
        this.page = 1;
      }
    }
  },
  mounted(){
    this.fetchCompanyDict();
    this.fetchList({
      company_id: this.filter.company_id,
      bill_status: this.stage,
      order: "asc",
      page: this.page,
      page_size: this.page_size,
      bill_status: this.noAction?this.filter.bill_status:this.stage,
      loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
      loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
    });
  }
}
</script>
<style>
  .m-order-table .el-table td{
    padding: 5px 0;
  }
  .m-order-table .search .el-select .el-input {
    width: 130px;
  }
</style>





