          <el-row :gutter="20" v-if="item.sign.fukuanshijian.length !== 0">
            <el-col :span="6" v-for="img in item.sign.fukuanshijian" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12"><span v-if="item.sign.qichezuling.length === 0"></span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.qichezuling.length !== 0">
            <el-col :span="6" v-for="img in item.sign.qichezuling" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12"><span v-if="item.sign.shouquan.length === 0"></span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.shouquan.length !== 0">
            <el-col :span="6" v-for="img in item.sign.shouquan" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12"><span v-if="item.sign.zulinghezhun.length === 0"></span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.zulinghezhun.length !== 0">
            <el-col :span="6" v-for="img in item.sign.zulinghezhun" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12"><span v-if="item.sign.other.length === 0"></span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.other.length !== 0">
            <el-col :span="6" v-for="img in item.sign.other" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
        </div>
      </el-tab-pane>
      <el-tab-pane v-if="stage > 4" label="" name="finance">
        <el-row>
          <el-col :span="12">{{ item.finance.status === 1?'':'' }}</el-col>
        </el-row>
        <el-row>
          <el-col :span="12">{{ item.finance.opinion }}</el-col>
        </el-row>
        <el-row>
          <el-col :span="12">(){{ item.finance.customer_name }}</el-col>
        </el-row>
        <el-row>
          <el-col :span="12">{{ item.finance.bank_name }}</el-col>
          <el-col :span="12">{{ item.finance.bank_number }}</el-col>
        </el-row>
        <el-row>
          <el-col :span="12">{{ item.finance.money_type }}</el-col>
          <el-col :span="12">() (){{ item.finance.money }}</el-col>
        </el-row>
        <el-row>
          <el-col :span="12">{{ item.finance.lean_type }}</el-col>
          <el-col :span="12">{{ item.finance.date }}</el-col>
        </el-row>
        <!-- <el-row>
          <el-col :span="12"></el-col>
        </el-row> -->
        <el-row>
          <table class="attachment_table" border="1" style="border-spacing: 0;">
              <tr>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td>{{ item.finance.attachment?item.finance.attachment[0].fee_descript:'' }}</td>
                <td>{{ item.finance.attachment?item.finance.attachment[0].fee:'' }}</td>
                <td>{{ item.finance.attachment?item.finance.attachment[0].bank_info:'' }}</td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td>{{ item.finance.attachment?item.finance.attachment[1].fee_descript:'' }}</td>
                <td>{{ item.finance.attachment?item.finance.attachment[1].fee:'' }}</td>
                <td>{{ item.finance.attachment?item.finance.attachment[1].bank_info:'' }}</td>
              </tr>
          </table>
        </el-row>
        <el-button style="margin-top: 20px;" type="primary" @click="onDownload"></el-button>
      </el-tab-pane>
    </el-tabs> 
  </el-dialog>
</template>
<script>
export default {
  props:['item','stage'],
  data(){
    return {
      visible: true,
      activeName: 'bill_info' 
    }
  },
  filters:{
    billStatusFilter(val){
      switch(val){
        case -1: return '';
        case 0: return '';
        case 1: return '';
        case 2: return '';
        case 3: return '';
        case 4: return '';
        case 5: return '';
      }
    },
    levelFilter(val){
      if(val >= 350 && val <= 500){
        return 'D';
      }else if(val >= 501 && val <= 600){
        return 'C';
      }else if(val >= 601 && val <= 700){
        return 'B';
      }else if(val >= 701 && val <= 800){
        return 'A';
      }else if(val >= 801 && val <= 1000){
        return 'AA';
      }else{
        return '';
      }
    },
    interestsFilter(val){
      let interests = [
        {name: '1.4%', value: 0.014},
        {name: '1.6%', value: 0.016},
        {name: '2.5%', value: 0.025},
        {name: '3%', value: 0.03},
        {name: '4%', value: 0.04}
      ]
      let target = interests.find(item => item.value === val);
      if(target){
        return target.name;
      }else{
        return val;
      }
    },
    cardDict(val, key){
      let card = {
        sex:[
          {name: '', value: 9},
          {name: '', value: 30}
        ],
        age:[
          {name: '18-25', value: 21},
          {name: '26-35', value: 42},
          {name: '36-45', value: 70},
          {name: '46-55', value: 28},
          {name: '56-60', value: 7}
        ],
        household:[
          {name: '', value: 100},
          {name: '', value: 60},
          {name: '', value: 40},
          {name: '', value: 10}
        ],
        marrie_status:[
          {name: '', value: 60},
          {name: '', value: 100},
          {name: '', value: 10}
        ],
        education:[
          {name: '', value: 40},
          {name: '', value: 24},
          {name: '', value: 12},
          {name: '', value: 4}
        ],
        living_status:[
          {name: '', value: 200},
          {name: '', value: 160},
          {name: '', value: 100},
          {name: '', value: 60},
          {name: '', value: 20}
        ],
        family:[
          {name: '', value: 100},
          {name: '', value: 60},
          {name: '', value: 10}
        ],
        work:[
          {name: '', value: 40},
          {name: '', value: 24},
          {name: '', value: 12},
          {name: '', value: 4}
        ],
        income:[
          {name: '10000', value: 12},
          {name: '10000-20000', value: 24},
          {name: '20000-30000', value: 36},
          {name: '30000-40000', value: 60},
          {name: '40000-50000', value: 84},
          {name: '50000', value: 120}
        ],
        loan_type:[
          {name: '', value: 48},
          {name: '', value: 80},
          {name: '', value: 8},
          {name: '', value: 24}
        ],
        car_policy:[
          {name: '', value: 60},
          {name: '', value: 24},
          {name: '', value: 6}
        ],
        face_review:[
          {name: '', value: 60},
          {name: '', value: 42},
          {name: '', value: 30},
          {name: '', value: 6}
        ]
      }
      let target = card[key].find(item => item.value === val);
      if(target){
        return target.name;
      }else{
        return '';
      }
    }
  },
  computed:{
    total_score(){
      let total = 0;
      for(let key in this.item.wind_control.score_card){
        if(key !== 'score' && key !== 'total_score'){
          total += this.item.wind_control.score_card[key];
        }
      }
      return total;
    }
  },
  methods:{
    closeEvent(){
      const that = this;
      this.visible = false;
      setTimeout(()=>{that.$emit('close');},500);
    },
    onDownload(){
      let uid = sessionStorage.getItem('ts_user_id');
      window.open(`/v1/inspect/${uid}/finance/file?finance_id=${this.item.finance.finance_id}`);
    }
  }
}
</script>
<style>
  .m-orderManage-detailDialog .detail-title {
    line-height: 24px;
    font-size: 16px;
    color: #2d2f33;
    border-bottom: 1px #ddd solid;
    margin-bottom: 10px;
  }
  .m-orderManage-detailDialog .detail-image{
    width:100%;
    padding:5px;
    border:1px #ddd solid;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .m-orderManage-detailDialog .scord_card_table td{
    min-width: 120px;
    padding: 0 10px;
  }
  .m-orderManage-detailDialog .attachment_table td{
    min-width: 200px;
    padding: 0 5px;
  }
</style>




